body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

h1 {
  margin-top: 3rem;
}

.layout {
  display: grid;
  grid-template-columns: auto 800px auto;
  grid-template-rows: 50px calc(100vh - 50px);
}

header,
.main {
  grid-column: 2 / span 1;
}

header {
  background-color: #333;
}

header ul {
  list-style: none;
  margin: 0;
  position: relative;
  text-align: right;
}

header li {
  display: inline-block;
}

header li:first-child {
  left: 0;
  position: absolute;
}

header li:first-child a {
  background: orange url("/avatar.svg") no-repeat left top;
  background-size: contain;
  height: 50px;
  padding: 0;
  width: 50px;
}

header li:first-child a span {
  display: none;
}

header a {
  display: block;
  color: #fff;
  line-height: 50px;
  padding-left: 15px;
  padding-right: 15px;
}

header a:hover {
  background-color: #444;
}

.main {
  padding: 0 10px;
}

a {
  color: orange;
}

.articles-list {
  list-style: none;
  margin: 3rem 0;
  padding: 0;
}

.articles-list a {
  display: block;
  text-decoration: none;
}

.articles-list li {
  font-weight: bold;
  font-size: 1.17em;
  margin-bottom: 15px;
  position: relative;
}

.articles-list a:hover {
  color: #333;
}

.articles-list .article-date {
  color: #444;
  display: block;
  font-size: 11px;
}

.articles-list .article-title {
  text-decoration: underline;
}

.loading {
  font-size: 1rem;
  line-height: calc(100vh - 50px);
  text-align: center;
}

article {
  margin-bottom: 2rem;
}



